
.tradeview {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.tradeview__wrapper {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.tradeview__main {
    position: relative;
    z-index: 10;
    -ms-flex-preferred-size: 0;
        flex-basis: 0;
    -webkit-box-flex: 1;
        -ms-flex-positive: 1;
            flex-grow: 1;
}
.tradeview__side {
    position: relative;
    z-index: 20;
    -ms-flex-preferred-size: 300px;
        flex-basis: 300px;
}
.tradeview__minicharts-wrapper {
    min-height: 300px;
    padding: 0 10px 20px;
    border: 1px solid #BFCBDA;
    border-radius: 5px;
    background-color: rgba(255, 255, 255, 0.95);
}
.tradeview__tradechart {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
}
.sidebar {
  -ms-flex-preferred-size: 300px;
      flex-basis: 300px;
  box-sizing: border-box;
  border: 3px solid black;
}
.controls {
  position: relative;
  z-index: 20;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.controls > div,
  .controls > button {
    margin-right: 10px;
}
.controls .el-select input {
    width: 80px;
}
.controls .el-input--small .el-input__inner {
    height: 36px;
}
.controls__title {
    margin: 11px 0;
    font: 500 14px/1.4 "Avenir", Helvetica, Arial, sans-serif;
}
.controls--ur {
    position: fixed;
    z-index: 20;
    right: 50px;
    top: 25px;
}
.controls--ur > div,
    .controls--ur > button {
      margin-right: 0;
}
.el-slider {
  width: 413px;
}
.el-slider__input {
    margin-top: 0;
}
.el-input-number--small .el-input-number__decrease,
.el-input-number--small .el-input-number__increase {
  line-height: 34px;
}



