
.tradegraph {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
}
.node.dragging .node__circle.c0 {
  opacity: 1;
  fill: #ff4949;
}
.node.dragging .node__circle.c1 {
  opacity: 1;
  fill: #ff4949;
}
.node.dragging .node__circle.c2 {
  opacity: 1;
  fill: #ff4949;
}
.node.dragging .node__circle.c3 {
  opacity: 1;
  fill: #ff4949;
}
.node.dragging .node__circle.c4 {
  opacity: 1;
  fill: #ff4949;
}
.node.active .node__circle {
  stroke-width: 4px;
  r: 30px;
}
.node.active .node__circle.c0 {
    fill: transparent;
}
.node.active .node__circle.c1 {
    fill: transparent;
}
.node.active .node__circle.c2 {
    fill: transparent;
}
.node.active .node__circle.c3 {
    fill: transparent;
}
.node.active .node__circle.c4 {
    fill: transparent;
}
.node.active .node__circle--active {
    r: 15px;
}
.node__edge {
  stroke-width: 1px;
  stroke: #475669;
  opacity: .2;
}
.node__text {
  font: bold 12px/1 Helvetica, Arial, sans-serif;
  text-transform: uppercase;
  fill: #324057;
  transition: all .2s;
  cursor: pointer;
}
.node__action {
  font: normal 12px/1 Helvetica, Arial, sans-serif;
  text-transform: uppercase;
  text-decoration: underline;
  fill: #0B9EFF;
}
.node__circle {
  position: relative;
  transition: all .2s ease-out;
  cursor: pointer;
  stroke-width: 0;
}
.node__circle:hover {
    opacity: .8;
}
.node__circle--active {
    transition: all .2s;
    pointer-events: none;
}
.node--firms .node__circle {
  fill: #13CE66;
  stroke: #ff4949;
}
.node--firms .node__circle.c0 {
    fill: #0a7138;
}
.node--firms .node__circle.c1 {
    fill: #13CE66;
}
.node--firms .node__circle.c2 {
    fill: #86f4b7;
}
.node--firms .node__circle--active {
    fill: #ff4949;
}
.node--consumers .node__circle {
  fill: #0B9EFF;
  stroke: #ff4949;
}
.node--consumers .node__circle.c0 {
    fill: #0063a4;
}
.node--consumers .node__circle.c1 {
    fill: #0B9EFF;
}
.node--consumers .node__circle.c2 {
    fill: #a4dbff;
}
.node--consumers .node__circle--active {
    fill: #ff4949;
}
.node--out {
  fill-opacity: .1;
  stroke-opacity: .1;
}
.links__wrapper {
  cursor: pointer;
}
.links__wrapper:hover text {
    fill: #1F2D3D;
}
.links__wrapper:hover .link {
    stroke: #13CE66;
}
.link {
  fill: none;
  stroke: rgba(0, 0, 0, 0.3);
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}
.link:nth-child(1) {
    -webkit-animation-delay: 0.2s;
            animation-delay: 0.2s;
}
.link:nth-child(2) {
    -webkit-animation-delay: 0.5s;
            animation-delay: 0.5s;
}
.link:nth-child(3) {
    -webkit-animation-delay: 0.6s;
            animation-delay: 0.6s;
}
.link:nth-child(4) {
    -webkit-animation-delay: 1s;
            animation-delay: 1s;
}
.link:nth-child(5) {
    -webkit-animation-delay: 0.4s;
            animation-delay: 0.4s;
}
.link:nth-child(6) {
    -webkit-animation-delay: 2.2s;
            animation-delay: 2.2s;
}
.link:nth-child(7) {
    -webkit-animation-delay: 1.2s;
            animation-delay: 1.2s;
}
.link:nth-child(8) {
    -webkit-animation-delay: 0.1s;
            animation-delay: 0.1s;
}
.link:nth-child(9) {
    -webkit-animation-delay: 3s;
            animation-delay: 3s;
}
.link:nth-child(10) {
    -webkit-animation-delay: 2.6s;
            animation-delay: 2.6s;
}
.link:nth-child(11) {
    -webkit-animation-delay: 2s;
            animation-delay: 2s;
}
.link:nth-child(12) {
    -webkit-animation-delay: 1.6s;
            animation-delay: 1.6s;
}
.link:nth-child(13) {
    -webkit-animation-delay: 0.4s;
            animation-delay: 0.4s;
}
.link:nth-child(14) {
    -webkit-animation-delay: 0.5s;
            animation-delay: 0.5s;
}
.link:nth-child(15) {
    -webkit-animation-delay: 0.1s;
            animation-delay: 0.1s;
}
.link:nth-child(16) {
    -webkit-animation-delay: 1.9s;
            animation-delay: 1.9s;
}
.link__label {
    -webkit-transform-origin: center center;
            transform-origin: center center;
    transition: -webkit-transform 1s 1s;
    transition: transform 1s 1s;
    transition: transform 1s 1s, -webkit-transform 1s 1s;
    font-size: 12px;
    fill: #324057;
}
.link__label.rot {
      -webkit-transform: rotate(180deg);
              transform: rotate(180deg);
}
.marker {
  fill: rgba(0, 0, 0, 0.3);
}
.contextmenu__list {
  padding: 0;
  margin: 0;
  list-style-type: none;
}
.contextmenu__item {
  display: block;
}
.contextmenu__item + .contextmenu__item {
    margin-top: 5px;
}
.contextmenu__btn {
  display: block;
  width: 100%;
}
@-webkit-keyframes pulsate {
0% {
    opacity: 1;
}
30% {
    opacity: .3;
}
100% {
    opacity: 1;
}
}
@keyframes pulsate {
0% {
    opacity: 1;
}
30% {
    opacity: .3;
}
100% {
    opacity: 1;
}
}



